
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent, ref } from "vue";
import { IonPage } from "@ionic/vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { HtsRecencyService } from "../../services/hts_recency_service";
import HisStandardForm from "@/components/Forms/HisStandardForm.vue";
import { StartHtsEncounter } from "@/apps/HTS/composables/HtsFormEncounter";

export default defineComponent({
  components: { IonPage, HisStandardForm },
  setup() {
    const fields = ref<Field[]>([])
    const service = new HtsRecencyService(-1)

    const { mapStrToOptions, saveEncounter, patientDashboardUrl } = StartHtsEncounter({
      onInitVisit: () => {
        fields.value = [ recencyTestField() ]
      }
    })

    function recencyTestField(): Field {
      return {
        id: "recency_test",
        helpText: "Recency Test",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          const concept = 'Recency Test'
          return {
            offlineMeta: {
              [concept]: v.value
            },
            obs: service.buildValueCoded(concept, v.value),
          };
        },
        options: () => {
          return mapStrToOptions([
            "Long-Term",
            "Recent",
            "Negative",
            "Invalid",
            "NOT done"
          ]);
        },
      };
    }

    function onFinish(_: any, computedData: any) {
      saveEncounter({ 
        encounterName: "RECENCY",
        encounterTypeID: service.encounterTypeID, 
        computedData 
      })
    }

    return {
      patientDashboardUrl,
      onFinish,
      fields
    }
  }
});
